import headtitle from '../components/header'
export default {
  props: [],
  findList: [],
  data() {
    return {
      findList: [1, 2, 3]
    }
  },
  components: {
    headtitle
  },
  methods: {

    Request() {

      var url = '/rest/find/findFeature';
      var that = this;
      that.axios.get(url)
        .then(response => {
          that.findList = response.data.data.featureList;
        })
    }
  },
  mounted() {

    this.active("#discovery");
    this.Request();

  },
  updata() {
    this.Request();

  }

}



